.projects {
    height: 67vh;
    padding: 1.5vh 0; }

/* nav buttons */
.projects button{
    background-color: white;
    font-weight: bold;
    width: 8vh;
    margin: 0.25vh;
    border: 0.2vh solid #282c34; }

.projects button:hover{
    background-color: #282c34;
    border: 0.1vh solid white; }

.projects button:disabled {
    visibility: hidden;
}

/* slider */
.cards-slider {
    position: relative;
    max-width: 50vh;
    margin: 0 auto;
    color: #282c34;
}

.cards-slider:after {
    content: '';
    display: block;
    width: 100%;
    height: 45vh;
    position: absolute;
    top: 0;
    left: 0;
}

/* slider-wrapper */
.cards-slider-wrapper {
    display: flex;
    position: absolute;
    transition: transform 300ms cubic-bezier(0.455, 0.03, 0.515, 0.955);
}

.cards-slider-wrapper .card {
    flex: 1;
    min-width: 48.5vh;
    min-height: 53.5vh;
    max-width: 48.5vh;
    max-height: 50.5vh;
    opacity: 0.7;
    transform: scale(0.7);
    box-shadow: none;
    border-color: white;
    transition: opacity 300ms linear, border-color 300ms linear, background-color 300ms linear, transform 300ms cubic-bezier(0.455, 0.03, 0.515, 0.955);
}


.col {
    height: 40vh;
    padding: 3vh 0;
    position: relative;
}

.col:before, .col:after {
    content: '';
    display: block;
    width: 50%;
    height: 120%;
    background: linear-gradient(to right, #282c34 5%, rgba(40, 44, 52, 0) 50%);
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
}

.col:after {
    left: auto;
    right: 0;
    background: linear-gradient(to left, #282c34 5%, rgba(255, 255, 255, 0) 50%);
}

