.card {
    box-shadow: 0 0.1vh 0.3vh rgba(0, 0, 0, 0.3);
    transition: all 0.3s linear;
    cursor: pointer;
    background-color: #747474;
    padding: 1vh;
    text-align: center;
    margin: 0 auto; }

.card-title {
    font-weight: bold;
}

.card-image {
    width: 100%;
    margin: 0 0 1.5vh 0;
    display: block; }

.card-image img{
    height: 100%;
    width: 100%;
    box-shadow: 0 0.1vh 0.3vh rgba(0, 0, 0, 0.6);
    margin: 0 0 1.5vh 0;
    display: block; }

.tags {
    font-weight: bold;
    text-align: left;
}

.card-content {
    padding: 1vh 2.5vh;
}

.read-me {
    position: relative;
    border: 0.1vh solid black;
    padding: 1vh;
    text-decoration: none;
}

.chip {
    display: inline-block;
    height: auto;
    font-size: 1.8vh;
    font-weight: 500;
    color: rgba(0, 0, 0, 0.6);
    line-height: 2.8vh;
    padding: 0 0.5vh;
    border-radius: 0.3vh;
    background-color: #e4e4e4;
    margin-bottom: 0.5vh;
    margin-right: 0.5vh;
    transition: all .15s ease-in-out;
}

